module.exports = "// 别删除 .--markdown，修改它包裹的内容\n.--markdown{\n  width: 100%;\n  @each $size in 1, 2, 3, 4, 5, 6{\n    h#{$size}{\n      color: #4251ff;\n      font-size: 1/$size * 1rem + 0.6rem;\n    }\n  }\n  p{\n    font-size: 0.95rem;\n    line-height: 1rem;\n  }\n  a[href]{\n  }\n  img{\n    display: block;\n    width: 80%;\n    margin: 0 auto;\n    object-fit: contain;\n    &[alt=sticker]{\n      display: inline-block;\n      width: 1.6rem;\n      height: 1.6rem;\n      margin: 0 0.3rem -0.2rem 0.3rem;\n    }\n  }\n  input[type=checkbox]{\n    margin: 0 0.5rem !important;\n    width: 1.2rem;\n    height: 1.2rem;\n    background: transparent !important;\n    border: 1px solid #434343;\n    appearance: none;\n    line-height: inherit;\n    &:checked{\n      background: #86ffe4 !important;\n    }\n  }\n  strong{\n  }\n  blockquote{\n  }\n  table{\n    width: 90%;\n    margin: 1rem auto;\n    tr{\n      td{\n      }\n    }\n    thead{\n    }\n    tbody{\n    }\n  }\n  ul{\n  }\n  ol{\n  }\n  ul, ol{\n    list-style: none;\n  }\n  li{\n  }\n}"